import React from 'react';

const PageOptions = ({ onSelectPage }) => {
  return (
    <div className="container">
      <h3 className="center-align">Additional tools</h3>
      <div className="row">
      <h5 className="center-align">This tools are made by specific demand</h5>
        <div className="col s12 center-align">
          <button
            className="btn waves-effect waves-light"
            onClick={() => onSelectPage('RAP_page')}
          >
            RAP - Regional annual plan
          </button>
          <button
            className="btn waves-effect waves-light"
            onClick={() => onSelectPage('GeomFix_page')}
            style={{ marginLeft: '10px' }}
          >
            Fixing geometry problems automatically
          </button>
          <button
            className="btn waves-effect waves-light"
            onClick={() => onSelectPage('ConvertDWG')}
            style={{ marginLeft: '10px' }}
          >
            Convert DWG to Report/Map/Layer by excel
          </button>

          <button
            className="btn waves-effect waves-light"
            onClick={() => onSelectPage('Deals_page')}
            style={{ marginLeft: '10px' }}
          >
            Find deals from excel
          </button>

          <button
            className="btn waves-effect waves-light"
            onClick={() => onSelectPage('FireIndex')}
            style={{ marginLeft: '10px' }}
          >
            Update layer in egol based on Excel
          </button>


        </div>
      </div>
    </div>
  );
};

export default PageOptions;

