import React from 'react';
import { Link } from 'react-router-dom';

import DeleteVirtces from './images_FIX_GEOM/DeleteVirtces.JPG';
import find_prob from './images_FIX_GEOM/find_prob.JPG';
import PROBELM from './images_FIX_GEOM/PROBELM.JPG';
import Result from './images_FIX_GEOM/Result.JPG';

import rap_toolbox from './data//FixGeometry.atbx'
import toolbox from './toolbox.png';

const GeomFix_page = ({ resetPageSelection }) => {
  return (
    <div className="container">
            <div className="center-align" style={{ marginTop: '20px' }}>
        <Link
          to="/PageOptions"
          className="btn waves-effect waves-light"
          onClick={resetPageSelection}
        >
          Back to Options
        </Link>
      </div>
      <h1 className="center-align">FIX Geometry Problems</h1>
      <p className="flow-text">
        This tool consists of two stages. First, use the "geom problems" tool to
        find the vertices that need to be deleted. Then, use the "Deleting
        vertices from layer" tool to fix the problem.
      </p>
      <div className="row">
        <div className="col s12 m6 l4">
          <div className="center-align">
            <p>
              <strong>Problem</strong>
            </p>
            <img
              src={PROBELM}
              alt="PROBELM"
              className="responsive-img"
              style={{
                maxWidth: '100%',
                maxHeight: 'auto',
                objectFit: 'contain',
              }}
            />
          </div>
        </div>
        <div className="col s12 m6 l4">
          <div className="center-align">
            <p>
              <strong>Stage 1</strong>
            </p>
            <img
              src={find_prob}
              alt="DeleteVirtces"
              className="responsive-img"
              style={{
                maxWidth: '100%',
                maxHeight: 'auto',
                objectFit: 'contain',
              }}
            />
          </div>
        </div>
        <div className="col s12 m6 l4">
          <div className="center-align">
            <p>
              <strong>Stage 2</strong>
            </p>
            <img
              src={DeleteVirtces}
              alt="find_prob"
              className="responsive-img"
              style={{
                maxWidth: '100%',
                maxHeight: 'auto',
                objectFit: 'contain',
              }}
            />
          </div>
        </div>
      </div>
      <div className="center-align">
        <p>
          <strong>Result</strong>
        </p>
        <img
          src={Result}
          alt="Result"
          className="responsive-img"
          style={{
            maxWidth: '100%',
            maxHeight: 'auto',
            objectFit: 'contain',
          }}
        />
      </div>
      <div className="center-align" style={{ marginTop: '20px' }}>
      </div>

      <div className="row" style={{ display: 'flex', justifyContent: 'center', gap: '150px' }}>
        <h5 className="center-align">Project Tool Box</h5>
        <div className="col s12 m4">

            <div className="card-image">
              <a href={rap_toolbox} download>
                <img src={toolbox} alt="Toolbox - RAP" style={{ width: '50%', height: 'auto' }} />
              </a>
            </div>
            <div className="card-content center-align">
            </div>

        </div>
      </div>


    </div>
  );
};

export default GeomFix_page;
