import React from 'react';

// import GeoAIImage from './GeoAIImage.JPG';

// import attach_geoai from  './data/attachments/GeoAI.pdf';

const ShadeAnalysis = () => {
    return (
        <div className="container">
          <h1 className="center-align">Shade Analysis</h1>
          <h4 className="center-align">Made for the Survey of Israel 2022</h4>
          <div className="row">
            <div className="col s12">
              <p className="flow-text">
                This project was created in response to a government decision to provide better shade coverage for pedestrians through the planting of trees. The decision, known as Resolution 1022, was issued by the Israeli government on January 23, 2022. You can read more about the resolution <a href="https://www.gov.il/he/departments/policies/dec1022_2022">here</a>.
              </p>
      
              <p className="flow-text">
                The project underwent numerous changes over time, with the final decision being to create tree coverage based on AI analysis. Some other options, such as obtaining shade from buildings and other objects at specific times of the day, were considered and partially implemented but not chosen. You can read the article in Haaretz newspaper <a href="https://www.haaretz.co.il/nature/2023-05-04/ty-article-magazine/.premium/00000187-d6b8-d9b4-abaf-febe57220000">here</a>.
              </p>
      
              <p className="flow-text"> 
                The link to the map on GovMap can be found <a href="https://www.govmap.gov.il/sites/Tree1022.html">here</a>.
              </p>
              
              <div className="center-align">
                <iframe
                  title="GovMap"
                  src="https://www.govmap.gov.il/sites/Tree1022.html"
                  width="100%"
                  height="1200"
                  frameBorder="0"
                  style={{border: "1px solid #ddd"}}
                ></iframe>
              </div>
            </div>
          </div>
        </div>
      );
};

export default ShadeAnalysis;

