import React from "react";
import 'materialize-css/dist/css/materialize.min.css';
import 'materialize-css/dist/js/materialize.min.js';

const Login_Fire = (props) => {
    const {email, setEmail, password, setPassword, handleLogin, handleSignup, hasAccount, setHasAccount, emailError, passwordError} = props;

    return (
        <section className="login">
            <div className="loginContainer row">
                <div className="col s12 m6 offset-m3">
                    <div className="card">
                        <div className="card-content">
                            <span className="card-title">Login</span>
                            <div className="input-field">
                                <input type="text" id="email" autoFocus required value={email} onChange={(e) => setEmail(e.target.value)} />
                                <label htmlFor="email">Username</label>
                                <p className="red-text">{emailError}</p>
                            </div>
                            <div className="input-field">
                                <input type="password" id="password" required value={password} onChange={(e) => setPassword(e.target.value)} />
                                <label htmlFor="password">Password</label>
                                <p className="red-text">{passwordError}</p>
                            </div>
                            <div className="btnContainer">
                                {hasAccount ? (
                                    <>
                                        <button className="waves-effect waves-light btn" onClick={handleLogin}>Sign in</button>
                                        <p>Don't have an account? <span className="blue-text" onClick={() => setHasAccount(!hasAccount)}>Sign up</span></p>
                                    </>
                                ) : (
                                    <>
                                        <button className="waves-effect waves-light btn" onClick={handleSignup}>Sign up</button>
                                        <p>Have an account? <span className="blue-text" onClick={() => setHasAccount(!hasAccount)}>Sign in</span></p>
                                    </>
                                )}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Login_Fire;