


import React from 'react';

import image from './images/image.jpeg';


const SegmentationAI = () => {
  return (
    <div className="container">
      <h1 className="center-align">segment-anything</h1>
      <div className="row">
        <div className="col s12">
          <p className="flow-text">
            Object segmentation is a critical process in geospatial data
            analysis and involves the identification and isolation of
            individual objects within a raster image. SAM and Mask R-CNN are
            powerful deep learning models that have been specifically designed
            to automate this process.
          </p>
          <p className="flow-text">
            By integrating these models into the tool, users can quickly and
            accurately segment any object within a raster image, even if it is
            complex or irregularly shaped. This can be particularly useful in
            the analysis of satellite imagery, aerial photographs, and LiDAR
            data, where object segmentation is often a crucial step in the
            analysis process.
          </p>
          <p className="flow-text">
            Overall, the tool provided by{' '}
            <a href="https://segment-anything.com/">segment-anything.com</a>{' '}
            can greatly improve the efficiency and accuracy of geospatial data
            analysis by providing a powerful and easy-to-use object
            segmentation solution.
          </p>
          <p className="flow-text">
            So what is this replacing?{' '}
            <a href="https://pro.arcgis.com/en/pro-app/latest/tool-reference/geoai/extract-features-using-ai-models.htm">
              Extract Features Using AI Models (GeoAI)
            </a>{' '}
            .
          </p>
        </div>
        <div className="col s12">

                <figure>
                  <img src={image} />
                  <figcaption>after segmentation</figcaption>
                </figure>

          </div>
        <div className="col s12">
          <p className="flow-text">
            For more, watch the video below:
          </p>
          <div className="video-container">
            <iframe
              title="SegmentationAI"
              src="https://www.youtube.com/embed/KYD2TafoR6I"
              frameBorder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              allowFullScreen
            ></iframe>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SegmentationAI;


