import React from 'react';
import { Link } from 'react-router-dom';

import presentDEALS from './presentDEALS.JPG';
import resultdeals from './resultdeals.JPG';
import attach_deals from './data/attachments/deals.pdf';

const Deals_page = ({ resetPageSelection }) => {
  return (
    <div className="container">
      <div className="row">
        <div className="col s12 m10 offset-m1">
          <div className="card">
            <div className="card-content">
              <div className="center-align">
                <Link to="/PageOptions" className="btn" onClick={resetPageSelection}>
                  Back to Options
                </Link>
              </div>
              <h4 className="center-align">Find deals from excel</h4>
              <p className="flow-text">
                This tool was created to find geometry for every deal in the excel file. The tool uses multiple layers and filters to find the most accurate geometry for every deal.
              </p>
              <div className="center-align">
                <img src={presentDEALS} alt="presentDEALS" className="responsive-img" />
              </div>
              <div className="center-align">
                <h5 className="blue-text text-darken-1" style={{ paddingTop: '20px', paddingBottom: '10px'}}>Results</h5>
                <img src={resultdeals} alt="resultdeals" className="responsive-img" />
              </div>
              <div className="center-align">
                <p>
                  <a className="waves-effect waves-light btn" href={attach_deals} target="_blank" rel="noopener noreferrer">
                    Download PDF
                  </a>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Deals_page;