import React from 'react';
import { Link } from 'react-router-dom';

import ConvertDWGPic from './ConvertDWGPic.JPG';

const ConvertDWG = ({ resetPageSelection }) => {
    return (
      <div className="container">
                <div className="center-align">
          <Link to="/PageOptions" className="btn" onClick={resetPageSelection}>
            Back to Options
          </Link>
        </div>
        <h1 className="center-align">Convert DWG to Report/Map/Layer by excel</h1>
        <p className="flow-text">
          Getting reports, create visualization, extract needed layers and information, perform geospatial and attributes checks, manipulations, and more on
          large-scale DWG files with just a simple excel.
        </p>
        <div className="center-align">
          <img src={ConvertDWGPic} alt="ConvertDWG" />
        </div>

      </div>
    );
  };
  
  export default ConvertDWG;
