import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Switch, Link, Redirect } from 'react-router-dom';
import Documentation from './Documentation';
import Login_Fire from './Login_Fire';
import GeoAI from './GeoAI';
import NetWork from './NetWork';
import PageOptions from './PageOptions';
import ShadeAnalysis from './ShadeAnalysis';
import RAP_page from './RAP_page';
import GeomFix_page from './GeomFix_page';
import HomePage from './HomePage';
import SegmentationAI from './SegmentationAI';
import ConvertDWG from './ConvertDWG';
import Deals_page from './Deals_page';
import FireIndex from './FireIndex';
import './App.css';

import { signInWithEmailAndPassword, createUserWithEmailAndPassword, onAuthStateChanged, signOut } from 'firebase/auth';
import auth from './Fire';

function App() {
  const [user, setUser] = useState('');
  const [email, setEmail] = useState('');
  const [emailError, setEmailError] = useState('');
  const [password, setPassword] = useState('');
  const [passwordError, setPasswordError] = useState('');
  const [hasAccount, setHasAccount] = useState(false);

  const clearInputs = () => {
    setEmail('');
    setPassword('');
  };

  const clearErrors = () => {
    setEmailError('');
    setPasswordError('');
  };

  const handleLogin = () => {
    clearErrors();
    signInWithEmailAndPassword(auth, email, password).catch(err => {
      switch (err.code) {
        case "auth/invalid-email":
        case "auth/user-disabled":
        case "auth/user-not-found":
          setEmailError(err.message);
          break;
        case "auth/wrong-password":
          setPasswordError(err.message);
          break;
        default:
          break;
      }
    });
  };

  const handleSignup = () => {
    clearErrors();
    createUserWithEmailAndPassword(auth, email, password).catch(err => {
      switch (err.code) {
        case "auth/email-already-in-use":
        case "auth/invalid-email":
          setEmailError(err.message);
          break;
        case "auth/weak-password":
          setPasswordError(err.message);
          break;
        default:
          break;
      }
    });
  };

  const handleLogout = () => {
    signOut(auth);
  };

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, user => {
      if (user) {
        clearInputs();
        setUser(user);
      } else {
        setUser('');
      }
    });

    return () => {
      unsubscribe();
    };
  }, []);

  const [selectedPage, setSelectedPage] = useState(null);

  const handlePageSelection = (page) => {
    setSelectedPage(page);
  };

  const resetPageSelection = () => {
    console.log('resetPageSelection');
    setSelectedPage(null);
  };

  return (
    <Router>
      <div>
        <nav>
          <ul className="nav-links">
            <li>
              <Link to="/">Home page</Link>
            </li>
            <li>
              <Link to="/Documentation">Documentation</Link>
            </li>
            <li>
              <Link to="/GeoAI">GeoAI</Link>
            </li>
            <li>
              <Link to="/NetWork">NetWork</Link>
            </li>
            <li>
              <Link to="/SegmentationAI">SegmentationAI</Link>
            </li>
            <li>
              <Link to="/ShadeAnalysis">Shade Analysis</Link>
            </li>
            <li>
              <Link to="/PageOptions">Options</Link>
            </li>
            {user ? (
              <>
                <li>
                  <Link to="/Login_Fire" onClick={handleLogout}>Logout</Link>
                  </li>
          </>
        ) : (
          <li>
            <Link to="/Login_Fire">LogIn</Link>
          </li>
        )}
      </ul>
    </nav>
    {user ? (
      
      <Switch>
        <Route path="/Documentation" render={(props) => <Documentation {...props} variableName={hasAccount} />} />
        <Route path="/GeoAI" component={GeoAI} />
        <Route path="/NetWork" component={NetWork} />
        <Route path="/SegmentationAI" component={SegmentationAI} />
        <Route path="/Login_Fire" component={Login_Fire} />

        <Route path="/ShadeAnalysis" component={ShadeAnalysis} />

        <Route path="/PageOptions">
          {selectedPage ? (
            <Redirect to={`${selectedPage}`} />
          ) : (
            <PageOptions onSelectPage={handlePageSelection} />
          )}
        </Route>
        <Route path="/RAP_page">
          <RAP_page resetPageSelection={resetPageSelection} />
        </Route>

        <Route path="/GeomFix_page">
          <GeomFix_page resetPageSelection={resetPageSelection} />
        </Route>

        <Route path="/ConvertDWG">
          <ConvertDWG resetPageSelection={resetPageSelection} />
        </Route>

        <Route path="/Deals_page">
          <Deals_page resetPageSelection={resetPageSelection} />
        </Route>

        <Route path="/FireIndex">
          <FireIndex resetPageSelection={resetPageSelection} />
        </Route>

        <Route path="/" component={HomePage} />
      </Switch>
    ) : (
      <Login_Fire
        email={email}
        setEmail={setEmail}
        password={password}
        setPassword={setPassword}
        handleLogin={handleLogin}
        handleSignup={handleSignup}
        hasAccount={hasAccount}
        setHasAccount={setHasAccount}
        emailError={emailError}
        passwordError={passwordError}
      />
    )}
  </div>
</Router>

  );  
}

export default App;
