import React from 'react';
import { Link } from 'react-router-dom';

import RAP_pic1 from './images_RAP/RAP1.JPG'
import RAP_pic2 from './images_RAP/RAP2.JPG'
import RAP_pic3 from './images_RAP/RAP3.JPG'
import RAP_pic4 from './images_RAP/RAP4.JPG'
import rap_toolbox from './data//RAP.atbx'
import toolbox from './toolbox.png';

const RAP_page = ({ resetPageSelection }) => {
  return (
    <div className="container">
            <div className="center-align">
        <Link to="/PageOptions" className="btn blue darken-3 larger-text" onClick={resetPageSelection}>
          Back to Options
        </Link>
      </div>
      <h1 className="center-align blue-text text-darken-3 larger-text">RAP - REGIONAL ANNUAL PLAN</h1>
      <p className="flow-text larger-text">
        This tool streamlines the process of working with layers using the arcpy "Create Relationship Class (Data Management)" function and saves on the standard license required. The tool consists of four key components:
      </p>
      <ul className="collection with-header larger-text">
        <li className="collection-header"><h5>1. Copy layer to edit</h5></li>
        <li className="collection-item">This step copies the data to a new layer for editing purposes.</li>
        <li className="collection-item"><img src={RAP_pic1} alt="RAP_pic1" /></li>
        <li className="collection-header"><h5>2. Push edit layer</h5></li>
        <li className="collection-item">This step inserts the updated data back into the connected tables, reflecting the changes made in the edit layer in the original tables.</li>
        <li className="collection-item"><img src={RAP_pic2} alt="RAP_pic2" /></li>
        <li className="collection-header"><h5>3. Multiple joins and updates with Excel</h5></li>
        <li className="collection-item">This step enables users to join and update data using information from an Excel file.</li>
        <li className="collection-item"><img src={RAP_pic3} alt="RAP_pic3" /></li>
        <li className="collection-header"><h5>4. Insert data from Excel</h5></li>
        <li className="collection-item">This step allows for importing new data from an Excel file directly into the layer.</li>
        <li className="collection-item"><img src={RAP_pic4} alt="RAP_pic4" /></li>
      </ul>

      <div className="row" style={{ display: 'flex', justifyContent: 'center', gap: '150px' }}>
        <h5 className="center-align">Project Tool Box</h5>
        <div className="col s12 m4">

            <div className="card-image">
              <a href={rap_toolbox} download>
                <img src={toolbox} alt="Toolbox - RAP" style={{ width: '50%', height: 'auto' }} />
              </a>
            </div>
            <div className="card-content center-align">
            </div>

        </div>
      </div>

    </div>
  );
};

export default RAP_page;