import React from 'react';
import { Link } from 'react-router-dom';

import FireIndexImage0 from './images_FireIndex/indexfire_imge0.JPG'
import FireIndexImage1 from './images_FireIndex/indexfire_imge1.JPG'
import FireIndexImage2 from './images_FireIndex/indexfire_imge2.JPG'


import attach_indexFire from './data/attachments/excel_raster.pdf';

const FireIndex = ({resetPageSelection}) => {
  return (
    <div className="container">

      <div className="center-align" style={{margin: '2rem 0'}}>
        <Link to="/PageOptions" className="btn blue darken-3" onClick={resetPageSelection}>
          Back to Options
        </Link>
      </div>

      <div className="column">
        <div className="col s12 m6 l4">
        <span className="flow-text larger-text"><strong>This tool was designed to automatically update data from the Israeli National Weather Service,
             in the kkl arcgis online server (agol). It will run on KKL server, connected to folder shared with INWS. </strong></span>
             <p></p>
             <span className="flow-text larger-text"><strong>General Scheme</strong></span>
          <div className="card">
            <div className="card-image">
              <img
                src={FireIndexImage0}
                alt="PROBELM"
                className="responsive-img"
              />
              
            </div>
          </div>
        </div>
        <div className="col s12 m6 l4">
        <span className="flow-text larger-text"><strong>INPUT</strong></span>
          <div className="card">
            <div className="card-image">
              <img
                src={FireIndexImage1}
                alt="DeleteVirtces"
                className="responsive-img"
              />
             
            </div>
          </div>
        </div>
        <div className="col s12 m6 l4">
        <span className="flow-text larger-text"><strong>OUTPUT</strong></span>
          <div className="card">
            <div className="card-image">
              <img
                src={FireIndexImage2}
                alt="find_prob"
                className="responsive-img"
              />
            </div>
          </div>
        </div>
        <div className="center-align">
                <p>
                  <a className="waves-effect waves-light btn" href={attach_indexFire} target="_blank" rel="noopener noreferrer">
                    Download PDF
                  </a>
                </p>
              </div>
      </div>
    </div>

  );
};

export default FireIndex;